<template>
  <div class="company-info">
    <div class="name">
      <h6>基础信息</h6>
      <el-button type="primary" link @click="handleEditor">编辑</el-button>
    </div>
    <div class="info">
      <p>
        <span>公司名称</span>
        <span>{{ companyInfo.companyNameStr }}</span>
      </p>
      <p>
        <span>创建人</span>
        <span>{{ companyInfo.createdByName }}</span>
      </p>
      <p>
        <span>手机号</span>
        <span>{{ companyInfo.createdByAccount }}</span>
      </p>
      <p>
        <span>认证状态</span>
        <span>
          {{ companyInfo.certificationStatusStr }}
        </span>
      </p>
      <p>
        <span>联系人</span>
        <span>{{ companyInfo.contactPersonStr }}</span>
      </p>
      <p>
        <span>联系方式</span>
        <span>{{ companyInfo.contactPhoneStr }}</span>
      </p>
      <p>
        <span>所属行业</span>
        <template v-if="companyInfo.industryName">
          <el-tooltip
            :content="companyInfo.industryNameStr"
            placement="top-start"
          >
            <span class="word-ellipsis">{{ companyInfo.industryNameStr }}</span>
          </el-tooltip>
        </template>
        <template v-else>
          <span>{{ companyInfo.industryNameStr }}</span>
        </template>
      </p>
      <p>
        <span>公司地区</span>
        <span>{{ companyInfo.companyAddress }}</span>
      </p>
      <p>
        <span>详细地址</span>
        <template v-if="companyInfo.addrDesc">
          <el-tooltip :content="companyInfo.addrDescStr" placement="top-start">
            <span class="word-ellipsis">{{ companyInfo.addrDescStr }}</span>
          </el-tooltip>
        </template>
        <template v-else>
          <span>{{ companyInfo.addrDescStr }}</span>
        </template>
      </p>
    </div>
    <!-- 编辑-->
    <EditorBasicInformationDialog
      v-model:visible="initData.editorVisible"
      :companyInfo="companyInfo"
      @emitFunc="getDetail"
    ></EditorBasicInformationDialog>
  </div>
</template>

<script lang="ts" setup>
import { shallowRef, reactive } from "vue";

import { useVueRouter } from "@/assets/useHook";
import { LocalStorage } from "jz-tool-lib";

import { customerDetailApi } from "@api/customer-management-api";
import EditorBasicInformationDialog from "@components/customer-management/personal-customer/detail/editor-basic-information-dialog.vue";

const { routeQuery } = useVueRouter();
const userInfo = LocalStorage.getItem("bd_userInfo");
const companyInfo = shallowRef({});

const initData = reactive({
  editorVisible: false,
  fullAddress: "",
});
const getDetail = async () => {
  try {
    const params = {
      parentId: userInfo.profileId,
      companyId: routeQuery.value.companyId,
    };
    const { data } = await customerDetailApi.getData(params);
    companyInfo.value = customerDetailApi.handleData(data);
  } catch (e) {
    console.log(e);
  }
};
getDetail();

//编辑
const handleEditor = () => {
  initData.editorVisible = true;
};
</script>
<style lang="scss" scoped>
.company-info {
  flex: 0 0 310px;
  height: 100%;
  background-color: #ffffff;

  .name {
    display: flex;
    padding: 20px;
    justify-content: space-between;
    border-bottom: 1px solid #edf1f6;

    h6 {
      font-size: 16px;
      font-weight: 900;
      color: #242934;
      line-height: 30px;
      word-break: break-all;
    }

    .icon {
      width: 14px;
      height: 14px;
      margin-right: 5px;
    }

    .certified {
      color: #9ca6b9;
      font-size: 12px;

      &.active {
        color: #2ed4a9;
      }
    }
  }

  .info {
    padding: 32px;

    p {
      display: flex;
      margin-bottom: 24px;

      span:nth-child(1) {
        flex: 0 0 80px;
        font-size: 14px;
        color: #9ca6b9;
        line-height: 20px;
      }

      span:nth-child(2) {
        flex-grow: 1;
        font-size: 14px;
        color: #646b7a;
        line-height: 20px;
      }
    }
  }
}

.word-ellipsis {
  white-space: nowrap;
  width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
