<template>
  <el-dialog
    width="750px"
    title="基础信息"
    class="custom-dialog"
    :model-value="visible"
    @close="dialogClose"
  >
    <div class="body-container">
      <el-form ref="ruleFormRef" :model="formData" :rules="rules">
        <el-row>
          <el-col :span="12">
            <el-form-item label="客户名称">{{
              companyInfo.companyName
            }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="创建人账号">{{
              companyInfo.createdByAccount
            }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="创建人姓名">{{
              companyInfo.createdByName
            }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="认证状态">
              {{ companyInfo.certificationStatusStr }}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="联系人姓名" prop="contactPerson">
              <el-input
                v-model="formData.contactPerson"
                class="box-input"
                clearable
                placeholder="请输入"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="联系方式" prop="contactPhone">
              <el-input
                v-model="formData.contactPhone"
                class="box-input"
                clearable
                placeholder="请输入"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="所属行业">
              <CustomIndustryCascade
                style="width: 200px"
                v-model="formData.industryId"
                :emitPath="true"
              ></CustomIndustryCascade>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="所属地区">
              <CustomChinaArea v-model="formData.areaCode"></CustomChinaArea>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="详情地址">
          <el-input
            v-model="formData.addrDesc"
            maxlength="100"
            class="box-textarea"
            placeholder="请输入"
            show-word-limit
            :rows="4"
            type="textarea"
          />
        </el-form-item>
      </el-form>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="dialogClose">取 消</el-button>
        <el-button type="primary" @click="handleSubmit">保 存</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { shallowReactive, ref, watch, defineAsyncComponent } from "vue";
import { ElMessage } from "element-plus";
import { updateDetailApi } from "@api/customer-management-api";
import { useVueRouter } from "@assets/useHook";

const { routeQuery } = useVueRouter();
const ruleFormRef = ref();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
  (e: "emitFunc");
}>();
const props = defineProps<{
  visible: boolean;
  companyInfo: any;
}>();
const formData = shallowReactive({
  contactPerson: "",
  contactPhone: "",
  addrDesc: "",
  industryId: [],
  areaCode: "",
  certificationStatusStr: "",
});

//所属行业
const CustomIndustryCascade = defineAsyncComponent(
  () => import("@components/common/custom-industry-cascade.vue")
);

//所属地区
const CustomChinaArea = defineAsyncComponent(
  () => import("@components/common/custom-china-area.vue")
);

//手机号校验
const rules = {
  contactPhone: [
    {
      required: false,
      pattern: /^1[3-9]\d{9}$/,
      message: "请输入正确的手机号",
      trigger: "blur",
    },
  ],
  contactPerson: [
    {
      required: false,
      max: 10,
      message: "联系人姓名最多输入10个字符",
      trigger: "blur",
    },
  ],
};

//保存
const handleSubmit = () => {
  ruleFormRef.value?.validate((valid) => {
    if (valid) {
      const industryIdString =
        Array.isArray(formData.industryId) && formData.industryId.length > 0
          ? formData.industryId.join(",")
          : "";
      const areaCodeArray = Array.isArray(formData.areaCode)
        ? formData.areaCode
        : [formData.areaCode];
      const lastAreaCode =
        areaCodeArray.length > 0 ? areaCodeArray[areaCodeArray.length - 1] : "";
      const params = {
        companyId: routeQuery.value.companyId,
        contactPerson: formData.contactPerson,
        contactPhone: formData.contactPhone,
        industryId: industryIdString,
        areaCode: lastAreaCode,
        addrDesc: formData.addrDesc,
      };
      updateDetailApi.getData(params).then((response) => {
        if (response.statusCode === 0) {
          ElMessage.success("编辑成功");
          emits("emitFunc");
          dialogClose();
        } else {
          ElMessage.error("编辑失败！");
        }
      });
    } else {
      console.log("表单验证失败");
      return false;
    }
  });
};

//关闭
const dialogClose = () => {
  emits("update:visible", false);
  formData.contactPerson = "";
  formData.contactPhone = "";
  formData.addrDesc = "";
};

watch(
  () => props.visible,
  (newVal) => {
    if (newVal) {
      formData.industryId = (props.companyInfo.industrId || "")
        .split(",")
        .map((id) => id.trim());
      formData.areaCode = props.companyInfo.areaCode;
      formData.contactPerson = props.companyInfo.contactPerson;
      formData.contactPhone = props.companyInfo.contactPhone;
      formData.addrDesc = props.companyInfo.addrDesc;
      formData.certificationStatusStr =
        props.companyInfo.certificationStatusStr;
    }
  }
);
</script>
<style lang="scss" scoped>
.box-input {
  width: 200px;
}
</style>
